div.highlight{
  padding: .5rem;
  border-radius: 10px;
  margin: 10px 0;
}
pre {
  margin: 0 0;
}

code:not(.highlight>code) {
  padding: 0 .2em;
  border-radius: .2em;
}

blockquote {
  padding-left: 1rem;
  border-left: 5px solid;
  color: #999;
}

a,
blockquote p {
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  text-align: center;
}

th, td{
  border: 1px solid #aaa;
  padding: .5em;
}

body[data-mode='dark']{
  code:not(.highlight>code) {
    background-color: #555;
    color: white;
  }
  .highlight {
    background-color: #252525;
  }
}

body[data-mode='light']{
  code:not(.highlight>code) {
    background-color: #bbb;
    color: #111;
  }
  .highlight {
    background-color: #555;
  }
}

p img{
  width: 100%;
}

@media (min-width: 1000px){
  p img{
    display: block;
    width: 50%;
    margin: 0 auto;
  }
}

h1 {
  margin-bottom: 1em;
  text-align: center;
}
